export const createCookie = () => {
  const date = new Date()
  date.setDate(date.getDate() + 1)

  let cookie = ''
  cookie += 'CookieName=Timeout;'
  cookie += `expires=${date.toUTCString()}`

  document.cookie = cookie
}

