export const getCookie = () => {
  const cookies = document.cookie.split(';')
  let cookie
  for (let i in cookies) {
    if ((cookie = cookies[i].search('Timeout') > 0)) {
      cookie = cookies[i].search('Timeout')
      break
    }
  }
  return cookie
}
