const useCurrentLocation = () => {
  const [location, setLocation] = useState({})
  useEffect(() => {
    const fetchCurrentLocation = async () => {
      try {
        const {
          coords: { latitude, longitude },
        } = await getCurrentLocation()
        setLocation({ latitude, longitude })
      } catch (error) {
        console.log(error)
      }
    }
    fetchCurrentLocation()
  }, [])

  return location
}

